"use client"

import type React from "react"
import { useState, useEffect } from "react"
import { apiCall } from "@/utils/api" // O tu ruta de api
import { Button } from "@/components/ui/custom-button"
import { Card } from "@/components/ui/custom-card"
import { Sparkles, Loader } from "lucide-react"

// Definimos las props que recibe del Dashboard
interface CreateRequestFormProps {
  userId: number
  userToken: string
  categories: any[]
  editingId: number | null
  initialForm: any
  onSuccess: () => void
  onCancel: () => void
}

export const CreateRequestForm: React.FC<CreateRequestFormProps> = ({
  userId,
  userToken,
  categories,
  editingId,
  initialForm,
  onSuccess,
  onCancel,
}) => {
  const [form, setForm] = useState({
    catId: 1,
    desc: "",
    price: "",
    addr: "",
    ref: "",
    lat: -8.11,
    lng: -79.03,
  })
  const [loading, setLoading] = useState(false)

  // Cargar datos si estamos editando
  useEffect(() => {
    if (editingId && initialForm) {
      setForm({
        catId: initialForm.catId,
        desc: initialForm.desc,
        price: initialForm.price,
        addr: initialForm.addr,
        ref: initialForm.ref || "",
        lat: initialForm.lat,
        lng: initialForm.lng,
      })
    }
  }, [editingId, initialForm])

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)

    const payload = {
      categoriaId: form.catId,
      descripcion: form.desc,
      precioSugerido: Number.parseFloat(form.price),
      direccion: form.addr,
      referenciaDireccion: form.ref || "Web App", // Ajustado a tu DTO
      latitud: form.lat,
      longitud: form.lng,
    }

    try {
      if (editingId) {
        // --- AQUÍ ESTÁ LA CORRECCIÓN CLAVE PARA EL PUT ---
        // Agregamos ?clienteId=... para que el Backend sepa quién edita
        await apiCall({
          endpoint: `/solicitudes/${editingId}?clienteId=${userId}`,
          method: "PUT",
          body: payload,
          token: userToken,
        })
        alert("Solicitud actualizada correctamente")
      } else {
        // CREAR (POST)
        await apiCall({
          endpoint: `/solicitudes?clienteId=${userId}`,
          method: "POST",
          body: payload,
          token: userToken,
        })
        alert("Solicitud creada exitosamente")
      }
      onSuccess()
    } catch (error) {
      console.error(error)
      alert("Error al guardar la solicitud")
    } finally {
      setLoading(false)
    }
  }

  return (
    <Card className="max-w-2xl mx-auto">
      <div className="p-6">
        <h3 className="text-lg font-bold mb-6 text-slate-800">
          {editingId ? "Editar Solicitud" : "Nueva Solicitud"}
        </h3>
        
        <form onSubmit={handleSubmit} className="space-y-4">
          {/* Selector de Categorías */}
          <div>
            <label className="block text-sm font-medium text-slate-700 mb-2">Categoría</label>
            <div className="flex gap-2 overflow-x-auto pb-2">
              {categories.map((c) => (
                <div
                  key={c.id}
                  onClick={() => setForm({ ...form, catId: c.id })}
                  className={`px-4 py-2 rounded-lg border cursor-pointer whitespace-nowrap transition-colors ${
                    form.catId === c.id
                      ? "bg-indigo-50 border-indigo-500 text-indigo-700"
                      : "bg-white hover:bg-slate-50 border-slate-200"
                  }`}
                >
                  {c.nombre}
                </div>
              ))}
            </div>
          </div>

          {/* Descripción */}
          <div>
            <div className="flex justify-between mb-1">
              <label className="block text-sm font-medium text-slate-700">Descripción</label>
              <button
                type="button"
                className="text-xs font-bold text-indigo-600 flex items-center gap-1 hover:text-indigo-700"
                onClick={() => alert("Conectar función de IA aquí")}
              >
                <Sparkles className="w-3 h-3" /> Mejorar con IA
              </button>
            </div>
            <textarea
              required
              className="w-full p-3 border border-slate-200 rounded-xl focus:ring-2 focus:ring-indigo-500 outline-none resize-none"
              rows={3}
              value={form.desc}
              onChange={(e) => setForm({ ...form, desc: e.target.value })}
              placeholder="Describe el problema..."
            />
          </div>

          {/* Precio y Dirección */}
          <div className="grid grid-cols-2 gap-4">
            <div>
              <label className="block text-sm font-medium text-slate-700 mb-1">Precio (S/.)</label>
              <input
                required
                type="number"
                className="w-full p-3 border border-slate-200 rounded-xl focus:ring-2 focus:ring-indigo-500 outline-none"
                value={form.price}
                onChange={(e) => setForm({ ...form, price: e.target.value })}
              />
            </div>
            <div>
              <label className="block text-sm font-medium text-slate-700 mb-1">Dirección</label>
              <input
                required
                type="text"
                className="w-full p-3 border border-slate-200 rounded-xl focus:ring-2 focus:ring-indigo-500 outline-none"
                value={form.addr}
                onChange={(e) => setForm({ ...form, addr: e.target.value })}
              />
            </div>
          </div>

          {/* Botones de Acción */}
          <div className="flex justify-end gap-3 pt-4">
            <Button variant="ghost" onClick={onCancel} type="button">
              Cancelar
            </Button>
            <Button type="submit" disabled={loading}>
              {loading ? <Loader className="animate-spin w-4 h-4 mr-2" /> : null}
              {editingId ? "Guardar Cambios" : "Publicar"}
            </Button>
          </div>
        </form>
      </div>
    </Card>
  )
}